.about {
    display: flex;
    align-items: center;
    padding: 40px 10%;
  }
  
  .about-photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-right: 20px;
  }
  
  .about h2 {
    font-size: 1.5em;
    margin-bottom: 10px;
  }
  
  .about p {
    color: #666;
    line-height: 1.6;
  }
  